<template>
  <h2>Add new photo:</h2>
  <p class="info_paragraph">Select location on the map ...</p>
  <form>
    <div id="map"></div>
    <p class="info_paragraph">or set it manually:</p>
    <p>latitude:</p>
    <input type="text" v-model="coords[0]" required>
    <p>longitude:</p>
    <input type="text" v-model="coords[1]" required>
    <p>
      <input type="submit" class="login" value="Save">
    </p>
  </form>

</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'
export default {

  data () {
    return {
      login: '',
      password: '',
      error: false,
      coords: []
    }
  },
  mounted() {
    this.$nextTick(function () {
        ymaps.ready( ()=> {
          const myMap = new ymaps.Map('map', {
            center: [53.893009, 27.567444],
            zoom: 10
          });
          myMap.events.add('click', (e) =>{
            if (!myMap.balloon.isOpen()) {
              this.coords = e.get('coords');
              myMap.balloon.open(this.coords, {
                contentHeader:'Photo was taken here!',
                contentBody:
                  '<p>Coordinates: ' + [
                    this.coords[0].toPrecision(6),
                    this.coords[1].toPrecision(6)
                  ]
              });
            }
            else {
              myMap.balloon.close();
            }
          })
        })
    })
  },
  methods: {
    Test(){
      console.log(this.coords[0])
    }
  }
}
</script>

<style>
#map{
  width: 600px;
  height: 400px;
  margin-left: 35%;
}
.info_paragraph{
  font-size: 25px;
}
</style>
